"use strict";
exports.__esModule = true;
var consumerLoan_1 = require("./consumerLoan");
var fastLoan_1 = require("./fastLoan");
var houseLoan_1 = require("./houseLoan");
var MIN_LOAN_SIZE = 0;
var MAX_FAST_LOAN_SIZE = 5000;
var MAX_CONSUMER_LOAN_SIZE = 10000;
var MAX_CONSUMER_LOAN_PERIOD = 60;
var MIN_LOAN_PERIOD = 0;
var MAX_HOUSE_LOAN_PERIOD = 360;
var MAX_FAST_LOAN_PERIOD = 24;
var MONTHS_IN_YEAR = 12;
var loanSubmit = document.getElementById("loanForm");
var formClass = loanSubmit.className;
if (formClass == "fastLoan") {
    loanSubmit.addEventListener("submit", calculateFastLoan);
}
else if (formClass == "houseLoan") {
    loanSubmit.addEventListener("submit", calculateHouseLoan);
}
else if (formClass == "consumerLoan") {
    loanSubmit.addEventListener("submit", calculateConsumerLoan);
}
function calculateFastLoan() {
    var loanSize = +document.getElementById("loanSize").value;
    var loanPeriod = +document.getElementById("loanPeriod").value;
    var errorMsg = document.getElementById("errorMsg");
    var text = document.getElementById("calculatedLoan");
    if (loanSize < MIN_LOAN_SIZE || loanSize > MAX_FAST_LOAN_SIZE) {
        text.textContent = "";
        errorMsg.textContent = "Maksimali paskolos suma ".concat(MAX_FAST_LOAN_SIZE, " eur!");
    }
    else if (loanPeriod < MIN_LOAN_PERIOD || loanPeriod > MAX_FAST_LOAN_PERIOD) {
        text.textContent = "";
        errorMsg.textContent = "Maksimalus terminas ".concat(MAX_FAST_LOAN_PERIOD, " m\u0117n.!");
    }
    else {
        errorMsg.textContent = "";
        var fastLoan = new fastLoan_1["default"](loanSize, loanPeriod);
        text.textContent = "M\u0117nesinis mok\u0117stis: ".concat(fastLoan.monthlyPayment());
    }
}
function calculateHouseLoan() {
    var loanSize = +document.getElementById("loanSize").value;
    var loanPeriod = +document.getElementById("loanPeriod").value;
    var salary = +document.getElementById("salary").value;
    var numOfChildren = +document.getElementById("numOfChildren").value;
    var periodInMonths = loanPeriod * MONTHS_IN_YEAR;
    var maxLoanSize = (salary / 5) * periodInMonths; //Atlyginimas padalinamas iš 5, kad atskirtų sumą, kurią klientas gali skirti paskolai
    console.log(maxLoanSize);
    var errorMsg = document.getElementById("errorMsg");
    var text = document.getElementById("calculatedLoan");
    if (loanSize < MIN_LOAN_SIZE || loanSize > maxLoanSize) {
        text.textContent = "";
        errorMsg.textContent = "Maksimali paskolos suma ".concat(maxLoanSize, " eur!");
    }
    else if (loanPeriod < MIN_LOAN_PERIOD || periodInMonths > MAX_HOUSE_LOAN_PERIOD) {
        text.textContent = "";
        errorMsg.textContent = "Maksimalus terminas ".concat(MAX_HOUSE_LOAN_PERIOD / MONTHS_IN_YEAR, " m.!");
    }
    else {
        errorMsg.textContent = "";
        var houseLoan = new houseLoan_1["default"](loanSize, periodInMonths, numOfChildren, salary);
        text.textContent = "M\u0117nesinis mok\u0117stis: ".concat(houseLoan.monthlyPayment());
    }
}
function calculateConsumerLoan() {
    var loanSize = +document.getElementById("loanSize").value;
    var loanPeriod = +document.getElementById("loanPeriod").value;
    var itemType = document.getElementById("itemType").value;
    var periodInMonths = loanPeriod * MONTHS_IN_YEAR;
    var errorMsg = document.getElementById("errorMsg");
    var text = document.getElementById("calculatedLoan");
    if (loanSize < MIN_LOAN_SIZE || loanSize > MAX_CONSUMER_LOAN_SIZE) {
        text.textContent = "";
        errorMsg.textContent = "Maksimali paskolos suma ".concat(MAX_CONSUMER_LOAN_SIZE, " eur!");
    }
    else if (loanPeriod < MIN_LOAN_PERIOD || periodInMonths > MAX_CONSUMER_LOAN_PERIOD) {
        console.log(periodInMonths);
        text.textContent = "";
        errorMsg.textContent = "Maksimalus terminas ".concat(MAX_CONSUMER_LOAN_PERIOD / MONTHS_IN_YEAR, " m.!");
    }
    else {
        errorMsg.textContent = "";
        var consumerLoan = new consumerLoan_1["default"](loanSize, periodInMonths, itemType);
        text.textContent = "M\u0117nesinis mok\u0117stis: ".concat(consumerLoan.monthlyPayment());
    }
}
